import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Popconfirm, Col, message } from 'antd';
import { TableData } from 'components/common';
import { getGradePartTrainType, deleteGradePartTrainType } from 'services/gradePartTrainType';

const urlForm = '#grade-part-train-type/form';

class GradePartTrainType extends Component {
  state = { reload: 0 };

  getGradePartTrainType = async () => {
    const res = await getGradePartTrainType();
    res.data.items.forEach(item => {
      item.id = `trainTypeId ${item.trainTypeId} gradePartId ${item.gradePartId}`;
    });
    return res;
  };

  columns = [
    {
      title: 'Mã thành phần điểm',
      dataIndex: 'gradePartCode',
      key: 'gradePartCode',
    },
    {
      title: 'Tên thành phần điểm',
      dataIndex: 'gradePart',
      key: 'gradePart',
    },
    {
      title: 'Hệ',
      dataIndex: 'trainType',
      key: 'trainType',
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      render: (text, record) => (
        <Fragment>
          <Popconfirm
            title="Bạn có muốn xóa?"
            okText="Xóa"
            cancelText="Hủy"
            onConfirm={() => this.handleDelete(record)}
          >
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ),
    },
  ];

  handleDelete = async record => {
    try {
      await deleteGradePartTrainType(record.gradePartId, record.trainTypeId);
      message.success('Xóa thành công');
      this.setState({ reload: this.state.reload + 1 });
    } catch (err) {
      message.error('Xóa không thành công');
    }
  };

  render() {
    const { reload } = this.state;
    return (
      <Card title="Tòa nhà">
        <Row>
          <Col span={24}>
            <BtnAddNew href={`${urlForm}/new`} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              bordered
              columns={this.columns}
              service={this.getGradePartTrainType}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <GradePartTrainType />
  </Page>
);



